import React from 'react'

function TextButton(params) {
  return (
    <button className="button" onClick={params.onClick} style={{border: 'none'}} >
      {params.icon &&
        <span className="icon is-small">
          <i className={params.icon}></i>
        </span>
      }
      <span style={{fontWeight: 'bold', color: '#D1D1D6'}}>{params.label}</span>
    </button>
  );
}

export default TextButton