import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import Tabs from './global/Tabs'
import PrimaryButton from './global/buttons/PrimaryButton'
import ShareButton from './global/buttons/ShareButton'
import EditButton from './global/buttons/EditButton'
import RepostButton from './global/buttons/RepostButton'
import Controls from './global/Controls'
import InlineIconLabel from './global/InlineIconLabel'

function JobsOverview({ checked: checkedProp, ...rest }) {
  let data = {
    activeJobs: [
      {
        position: 'Frontend Developer',
        location: 'London, UK',
        new_messages: 2,
        new_matches: 8,
        in_processes: 4,
        candidates_accepted: 4,
        days_until_expiry: 10
      },
      {
        position: 'Java Developer',
        location: 'London, UK',
        new_messages: 0,
        new_matches: 15,
        in_processes: 2,
        candidates_accepted: 0,
        days_until_expiry: 12
      },
      {
        position: 'Java Developer',
        location: 'London, UK',
        new_messages: 1,
        new_matches: 15,
        in_processes: 2,
        candidates_accepted: 3,
        days_until_expiry: 15
      },
      {
        position: 'Junior Developer',
        location: 'London, UK',
        new_messages: 1,
        new_matches: 8,
        in_processes: 3,
        candidates_accepted: 0,
        days_until_expiry: 29
      }
    ],
    archivedJobs: [
      {
        position: 'Frontend Developer',
        location: 'London, UK',
        conversations: 25,
        candidates_forwarded: 4,
        expiration_date: '15/08/2020'
      }
    ]
  }

  const [index, setIndex] = useState(0)
  return (
    <div>
      <div className='columns'>
        <div className='column is-6'>
          <Tabs
            tabs={['Active Jobs', 'Archived Jobs']}
            position={index}
            setPosition={setIndex}
          />
        </div>
        <div className='column is-6' style={{ textAlign: 'right' }}>
          <Link to='/post'>
            <PrimaryButton
              label='Post New Job'
            />
          </Link>

        </div>
      </div>
      {index === 0 && data.activeJobs && data.activeJobs.map(activeJob => (
        <JobOverview
          position={activeJob.position}
          location={activeJob.location}
          new_messages={activeJob.new_messages}
          controls={[
            <Link to='/edit' className='icon-link'><EditButton /></Link>,
            <ShareButton />
          ]}
        >
          <div className='columns is-gapless is-marginless' style={{ paddingTop: 24, paddingBottom: 12 }}>
            <div className='column is-2'>
              <Link to='/job/abc/candidates'>
                <InlineIconLabel icon='fas fa-rocket'>
                  <strong>{activeJob.new_matches}</strong> New Matches
                </InlineIconLabel>
              </Link>
            </div>
            <div className='column is-2'>
              <Link to='/job/abc/candidates/in-process'>
                <InlineIconLabel icon='fas fa-comment-dots'>
                  <strong>{activeJob.in_processes}</strong> In Process
                </InlineIconLabel>
              </Link>
            </div>
            <div className='column is-3'>
              <Link to='/job/abc/candidates/candidates-accepted'>
                <InlineIconLabel icon='fas fa-user-check'>
                  <strong>{activeJob.candidates_accepted}</strong> Candidates Accepted
                </InlineIconLabel>
              </Link>
            </div>
            <div className='column is-5' style={{ textAlign: 'right', paddingTop: 2, fontSize: 14, color: '#9B9B9B' }}>
              <strong style={{ color: '#9B9B9B' }}>Days until job expires:</strong> {activeJob.days_until_expiry}
            </div>
          </div>
        </JobOverview>
      ))}

      {index === 1 && data.archivedJobs && data.archivedJobs.map(archivedJob => (
        <JobOverview
          position={archivedJob.position}
          location={archivedJob.location}
          controls={[
            <RepostButton />
          ]}
        >
          <div className='columns is-gapless is-marginless' style={{ paddingTop: 24, paddingBottom: 12 }}>
            <div className='column is-2'>
              <InlineIconLabel icon='fas fa-rocket' color='#D1D1D6'>
                <b>{archivedJob.conversations}</b> Conversations
              </InlineIconLabel>
            </div>
            <div className='column is-3'>
              <InlineIconLabel icon='fas fa-comment-dots' color='#D1D1D6'>
                <b>{archivedJob.candidates_forwarded}</b> Candidates Forwarded
              </InlineIconLabel>
            </div>
            <div className='column is-7' style={{ textAlign: 'right', paddingTop: 2, fontSize: 14, color: '#9B9B9B' }}>
              <strong style={{ color: '#9B9B9B' }}>Expiration date:</strong> {archivedJob.expiration_date}
            </div>
          </div>
        </JobOverview>
      ))}

    </div >
  )
}

export default JobsOverview

function JobOverview(params) {
  return (
    <div style={{ borderBottom: '1px solid #E4E4E4', marginBottom: 24 }}>
      <div className='columns is-gapless is-marginless'>
        <div className='column is-6'>
          <span>
            <h1 style={{ display: 'inline', marginRight: 12 }}>{params.position}</h1>
            <InlineIconLabel icon='fas fa-map-marker' color='#D1D1D6'>
              {params.location}
            </InlineIconLabel>
          </span>
        </div>
        <div className='column is-4' style={{ marginTop: 12 }}>
          {(params.new_messages || params.new_messages === 0) && <span
            style={{
              backgroundColor: `${params.new_messages > 0 ? '#956EDC' : '#E4E4E4'}`,
              color: '#FFFFFF',
              padding: 4,
              paddingLeft: 8,
              paddingRight: 8,
              fontSize: 14,
            }}
          >
            <strong style={{ color: '#FFFFFF' }}>{params.new_messages}</strong> New Message{params.new_messages > 1 ? 's' : ''}
          </span>}

        </div>
        <div className='column is-2' style={{ textAlign: 'right', marginTop: 16 }}>
          <Controls
            right={params.controls}
          />
        </div>
      </div>
      {params.children}
    </div>
  )
}