import React, { useState } from 'react'
import { LightGrey } from './global/colors'
import ListInput from './ListInput'
import RadioInput from './RadioInput'
import NumberInput from './global/NumberInput'
import TextInput from './global/TextInput'
import IconButton from './global/buttons/IconButton'
import AutoSuggestListInput from './global/AutoSuggestListInput'

function PostSalaryBenefits(params) {

  const [otherBenefitsInput, setOtherBenefitsInput] = useState('');
  const onKeyPressOtherBenefitsInput = function (event) {
    if (event.key === 'Enter') {
      let otherBenefits = [...params.otherBenefits]
      otherBenefits.push(otherBenefitsInput)
      params.setOtherBenefits(otherBenefits)
      setOtherBenefitsInput('')
    }
  }
  const onClickOtherBenefitsInput = function (value) {
    let otherBenefits = [...params.otherBenefits]
    otherBenefits.push(value)
    params.setOtherBenefits(otherBenefits)
    setOtherBenefitsInput('')
  }

  const [bonusInput, setBonusInput] = useState('');
  const onKeyPressBonusInput = function (event) {
    if (event.key === 'Enter') {
      let bonus = { ...params.bonusStructure }
      bonus[event.target.value] = 50
      params.setBonusStructure(bonus)
      setBonusInput('')
    }
  }

  const onClickBonusInput = () => {
    let bonus = { ...params.bonusStructure }
    bonus[bonusInput] = 50
    params.setBonusStructure(bonus)
    setBonusInput('')
  }

  const onChangeBonusStructureSlider = (key) => {
    return (event) => {
      let bonus = { ...params.bonusStructure }
      bonus[key] = Math.round(event.target.value / 10) * 10
      params.setBonusStructure(bonus)
    }
  }

  const deleteBonusStructure = (key) => {
    return (event) => {
      let bonus = { ...params.bonusStructure }
      delete bonus[key]
      params.setBonusStructure(bonus)
    }
  }

  const onDeleteOtherBenefits = function (index) {
    return function (event) {
      let otherBenefits = [...params.otherBenefits]
      otherBenefits.splice(index, 1);
      params.setOtherBenefits(otherBenefits)
    }
  }

  const languages = [
    'Free Fruit',
    'Free Coffee drinks',
    'Discounted meals',
    'Dental care plans',
    'Free travel',
    'Subsided travel',
    'Travel Loan',
    'Free company events',
    'Childcare vouchers',
    'Flexible working hours',
    'Gym Membership',
    'Yearly Company Holiday',
    'Xmas Food hamper',
    'Cycle Scheme',
    'Shopping Vouchers',
    'Personal Loans',
    'Course Allowance',
    'Car Allowance',
  ]

  return (
    <div>
      <h3>Salary</h3>
      <div className="columns">
        <div className="column is-6">
          <div style={{ fontWeight: 'bold', color: LightGrey, margin: 8 }}>
            Currency
          </div>
          <div className="select">
            <select onChange={event => params.setCurrency(event.target.value)}>
              <option selected={params.currency === 'GBP'}>GBP</option>
              <option selected={params.currency === 'EUR'}>EUR</option>
              <option selected={params.currency === 'USD'}>USD</option>
            </select>
          </div>
        </div>
      </div>

      <div className="columns" style={{ marginBottom: 0, paddingBottom: 0 }}>
        <div className="column is-2" style={{ marginBottom: 0, paddingBottom: 0 }}>
          <div style={{ fontWeight: 'bold', color: LightGrey }}>
            From
          </div>
        </div>
        <div className="column is-1" style={{ marginBottom: 0, paddingBottom: 0 }}>
        </div>
        <div className="column is-2" style={{ marginBottom: 0, paddingBottom: 0 }}>
          <div style={{ fontWeight: 'bold', color: LightGrey }}>
            To
          </div>
        </div>
      </div>

      <div className="columns">
        <div className="column is-2">
          <NumberInput
            value={params.minSalary}
            onChange={event => params.setMinSalary(event.target.value)}
          />
        </div>
        <div className="column is-1" style={{ borderBottom: '1px solid ' + LightGrey, height: 32 }}>
        </div>
        <div className="column is-2">
          <NumberInput
            value={params.maxSalary}
            onChange={event => params.setMaxSalary(event.target.value)}
          />
        </div>
      </div>

      <h3>Bonus</h3>
      <div className="columns">
        <div className="column is-6">
          <TextInput
            value={params.bonus}
            onChange={event => params.setBonus(event.target.value)}
            placeholder='' />
        </div>
      </div>

      <div style={{ fontWeight: 'bold', color: LightGrey, margin: 8 }}>
        How is it achieved?
      </div>
      <div className="columns">
        <div className="column is-6">
          <ListInput
            value={bonusInput}
            onChange={event => setBonusInput(event.target.value)}
            onKeyPress={onKeyPressBonusInput}
            placeholder='Personal Performance, Stocks...'
            iconOnClick={onClickBonusInput}
          />
          {params.bonusStructure && Object.entries(params.bonusStructure).map(([text, value]) => (
            <div className='columns' key={text}>
              <div className="column">
                <div
                  style={{
                    marginTop: 16,
                    fontWeight: 'bold',
                    fontSize: 16,
                  }}>
                  {text}
                </div>

              </div>
              <div className="column is-4">
                <div size='small' margin={{ left: 'small', right: 'small' }}>
                  <input className="slider is-fullwidth" step="1" min="0" max="100" type="range"
                    value={value}
                    onChange={onChangeBonusStructureSlider(text)} />
                </div>
              </div>
              <div className="column is-1" style={{ paddingTop: 26 }}>{value}%</div>
              <div className="column is-1" style={{ paddingTop: 26 }}>
                <IconButton
                  icon='fas fa-trash'
                  onClick={deleteBonusStructure(text)}
                />
              </div>
            </div>
          ))
          }
        </div>
      </div>

      <h3>Equity</h3>
      <RadioInput
        name='equity'
        value={params.equity}
        onChange={params.setEquity}
        options={[
          {
            label: 'Yes',
            value: 'true'
          },
          {
            label: 'No',
            value: 'false'
          }]
        }
      />

      <h3>Holiday</h3>
      <div className="columns">
        <div className="column is-2">
          <span className="field is-horizontal">
            <NumberInput
              value={params.holiday}
              onChange={event => params.setHoliday(event.target.value)}
            />
            <span style={{ fontSize: '1rem', marginTop: 8, marginLeft: 8 }}>Days</span>
          </span>
        </div>
      </div>

      <h3>Healthcare</h3>
      <div className="columns">
        <div className="column is-6">
          <div className="select">
            <select onChange={event => params.setHealthcare(event.target.value)}>
              <option selected={params.healthcare === 'None'}>None</option>
              <option selected={params.healthcare === 'Vitality - Basic Membership'}>Vitality - Basic Membership</option>
              <option selected={params.healthcare === 'Vitality - Premium Membership'}>Vitality - Premium Membership</option>
              <option selected={params.healthcare === 'BUPA Cover'}>BUPA Cover</option>
              <option selected={params.healthcare === 'Other'}>Other</option>
            </select>
          </div>
        </div>
      </div>

      <h3>Other Benefits</h3>
      <div className="columns">
        <div className="column is-6">
          <AutoSuggestListInput
            value={otherBenefitsInput}
            onChange={event => setOtherBenefitsInput(event.target.value)}
            onKeyPress={onKeyPressOtherBenefitsInput}
            placeholder='Personal Performance, Stocks...'
            addToList={onClickOtherBenefitsInput}
            data={languages}
          />
          {
            params.otherBenefits && params.otherBenefits.map((text, i) => (
              <div className='columns' key={text}>
                <div className="column">
                  <div
                    style={{
                      marginTop: 16,
                      fontWeight: 'bold',
                      fontSize: 16,
                    }}>
                    {text}
                  </div>
                </div>
                <div className="column is-1" style={{ paddingTop: 26 }}>
                  <IconButton
                    icon='fas fa-trash'
                    onClick={onDeleteOtherBenefits(i)}
                  />
                </div>
              </div>
            ))
          }
        </div>
      </div>
    </div >
  );
}

export default PostSalaryBenefits