import React from 'react'

function Modal(params) {

    let buttons = params.buttons
    if (params.buttons instanceof Array) {
        buttons = []
        buttons = params.buttons.map(node => {
            return <span style={{marginLeft: 12, marginRight: 12}}>{node}</span>
        })
    }

    return (
        <div className="modal is-active">
            <div className="modal-background" onClick={params.closeOnClick}></div>
            <div className="modal-content" style={{ borderRadius: 12, backgroundColor: 'white', padding: 20, textAlign: 'center' }}>
                <img
                    src={params.imageSrc}
                    alt=''
                    width={300}
                />
                {params.children}
                <div style={{ paddingTop: 24 }}>
                    {buttons}
                </div>
            </div>
            <button className="modal-close is-large" aria-label="close" onClick={params.closeOnClick}></button>
        </div>
    )
}

export default Modal