const monthNames = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun',
  'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'
];

const randomDate = () => {
  var randomMonth = parseInt(Math.random() * (12 - 1) + 1);

  var randomYear = parseInt(Math.random() * (2020 - 1980) + 1980);
  return monthNames[randomMonth] + ' ' + randomYear
}

const workDescriptions = [
  'What a banger of a job this was.',
  'Absolutely smashed it.',
  'Whopper of a job',
  'One of the best'
]

const randomWorkExperience = () => {
  return {
    company: random('companies'),
    role: random('roles'),
    start_date: randomDate(),
    end_date: randomDate(),
    description: random('workDescriptions')
  }
}

const randomWorkExperiences = () => {
  let work = []
  let i = 0
  for (i = 0; i <= getRandomInt(1, 6); i++) {
    work.push(randomWorkExperience())
  }
  return work
}

const randomQualifications = () => {
  let work = []
  let i = 0
  for (i = 0; i <= getRandomInt(0, 10); i++) {
    work.push(random('qualifications'))
  }
  return work
}

const randomTechSkills = () => {
  let work = []
  let i = 0
  for (i = 0; i <= getRandomInt(0, 20); i++) {
    work.push({ name: random('techLanguages'), experience: getRandomInt(1, 3) })
  }
  for (i = 0; i <= getRandomInt(0, 10); i++) {
    work.push({ name: random('softLanguages'), experience: getRandomInt(1, 3) })
  }
  return work
}

export const randomCandidate = () => {
  return {
    name: random('names'),
    id: create_UUID(),
    location: random('locations'),
    education: random('education'),
    github_url: random('github'),
    stackoverflow_url: random('stackoverflow'),
    signup_date: '2020',
    profile_picture: random('pictures'),
    tech_stack: randomTechSkills(),
    job_matches: getRandomInt(1, 50),
    active_processes: getRandomInt(1, 10),
    work: randomWorkExperiences(),
    qualifications: randomQualifications(),
  }
}

const randomCandidateWithUUID = (uuid) => {
  let candidate = randomCandidate()
  candidate.id = uuid
  return candidate
}

