import React, { useEffect, useState } from 'react'
import IconLabelButton from './IconLabelButton'
import PrimaryButton from './PrimaryButton'
import Controls from '../Controls'

function ReportButton() {

  const [open, setOpen] = useState(false)
  const [reportReason, setReportReason] = useState(false)

  return (
    <div>
      {open && <div className="modal is-active">
        <div className="modal-background" onClick={() => setOpen(false)}></div>
        <div className="modal-content" style={{ backgroundColor: 'white', padding: 20 }}>
          <h3>Report</h3>
          <p>Why are you reporting this?</p>
          <RadioInput
            name='reportReason'
            value={reportReason}
            onChange={setReportReason}
            options={[
              {
                label: 'Fake Advert',
                value: 'fake',
                checked: () => reportReason === 'fake',
                onChange: event => setReportReason(event.target.value)
              },
              {
                label: 'Plagiarism',
                value: 'plagiarism',
                checked: () => reportReason === 'plagiarism',
                onChange: event => setReportReason(event.target.value)
              },
              {
                label: 'Offensive Language',
                value: 'offensive',
                checked: () => reportReason === 'offensive',
                onChange: event => setReportReason(event.target.value)
              },
              {
                label: 'Other',
                value: 'other',
                checked: () => reportReason === 'other',
                onChange: event => setReportReason(event.target.value)
              }]
            }
          />
          <Controls
            right={[
              <PrimaryButton
                label='Send'
                onClick={() => setOpen(false)}
              />
            ]}
          />

        </div>
        <button className="modal-close is-large" aria-label="close" onClick={() => setOpen(false)}></button>
      </div>}
      <IconLabelButton
        label='Report'
        icon='fas fa-flag'
        style={{ fontSize: 16, color: '#D1D1D6' }}
        labelStyle={{ color: '#D1D1D6', fontWeight: 600, fontSize: 12 }}
        onClick={() => setOpen(true)}
      />
    </div>
  );
}

export default ReportButton

function RadioInput(params) {
  return (
    <div className="field">
      {params.options && params.options.map(option => (
        <div key={option.value} style={{ margin: 8 }}>
          <span onClick={event => params.onChange(option.value)}>
            <input
              className="is-checkradio is-primary"
              type="radio"
              name={params.name}
              checked={option.value === params.value ? "checked" : ""}
              value={option.value}
              onClick={event => params.onChange(option.value)}
            />
            <label>{option.label}</label>
          </span>
        </div>
      ))}
    </div>
  );
}