import React, { useState } from 'react'
import Autosuggest from 'react-autosuggest'

function AutoSuggestListInput(params) {

  // Teach Autosuggest how to calculate suggestions for any given input value.
  const getSuggestions = value => {
    const inputValue = value.trim().toLowerCase();
    const inputLength = inputValue.length;

    return inputLength === 0 ? [] : params.data.filter(lang =>
      lang.toLowerCase().includes(inputValue)
    )
  }

  // When suggestion is clicked, Autosuggest needs to populate the input
  // based on the clicked suggestion. Teach Autosuggest how to calculate the
  // input value for every given suggestion.
  const getSuggestionValue = suggestion => suggestion.name;

  // Use your imagination to render suggestions.
  const renderSuggestion = suggestion => (
    <div style={{ backgroundColor: '#FFFFFF', padding: 8, width: '100%', cursor: 'pointer' }}>
      {suggestion}
    </div>
  )

  const [value, setValue] = useState('')
  const [suggestions, setSuggestions] = useState([])

  const inputProps = {
    value,
    onChange: (event, { newValue, method }) => {
      setValue(newValue)
    }
  }

  const onSuggestionsFetchRequested = ({ value }) => {
    setSuggestions(getSuggestions(value));
  }

  const onSuggestionsClearRequested = () => {
    setSuggestions([]);
  }

  const iconOnClick = () => {
    params.addToList(value)
    setValue('')
  }

  const suggestionOnClick = (suggestion) => {
    params.addToList(suggestion)
    setValue('')
  }

  const renderInputComponent = inputProps => (
    <div className="control has-icons-right">
      <input
        className="input"
        type="text"
        onKeyPress={event => {
          if (event.key === 'Enter') {
            suggestionOnClick(value)
          }
        }}
        // onChange={params.onChange}
        // onKeyPress={params.onKeyPress}
        // value={params.value}
        // placeholder={params.placeholder}
        {...inputProps}
      />
      <span className="icon is-small is-right" style={{ pointerEvents: 'initial', cursor: 'pointer' }} onClick={iconOnClick}>
        <i className="fas fa-plus-circle" ></i>
      </span>
    </div>
  )

  const renderSuggestionsContainer = ({ containerProps, children, query }) => {
    return (
      <div {...containerProps} style={{ zIndex: 50, position: 'absolute', width: '100%' }}>
        <div className='columns'>
          <div className='column is-6'>
            {children}
          </div>
        </div>
      </div>
    );
  }
  return (
    <Autosuggest
      suggestions={suggestions}
      onSuggestionsFetchRequested={onSuggestionsFetchRequested}
      onSuggestionsClearRequested={onSuggestionsClearRequested}
      onSuggestionSelected={(event, { suggestion }) => suggestionOnClick(suggestion)}
      onSuggestionHighlighted={suggestion => console.log("yo yo yo", suggestion)}
      getSuggestionValue={getSuggestionValue}
      renderSuggestion={renderSuggestion}
      renderSuggestionsContainer={renderSuggestionsContainer}
      inputProps={inputProps}
      renderInputComponent={renderInputComponent}
    />
  );
}

export default AutoSuggestListInput