import React from 'react'
import { BrowserRouter as Router, Route } from 'react-router-dom'
// import Home from './Home'
import Job from './Job'
import JobForm from './JobForm'
import Profile from './Profile'
import EditJobForm from './EditJobForm'
import JobsOverview from './JobsOverview'

import NavBar from './NavBar'
import {randomFooterMessages} from './global/mock'

import './App.sass'
import './App.css'
import JobApplicants from './JobApplicants'

function App() {
  console.log("v7")
  fetch('https://0jzvzdhkz4.execute-api.eu-west-1.amazonaws.com/prod/testing')
    .then(res => res.json())
    .then(
      (result) => {
        console.log(result)
      })
  return (
    <Router basename={process.env.REACT_APP_ROUTER_BASENAME}>
      <NavBar />
      <section className="section">
        <div className="container">
          <Route exact path='/' component={Job} />
          <Route exact path='/post' component={JobForm} />
          <Route exact path='/edit' component={EditJobForm} />
          <Route exact path='/candidate/:id' component={Profile} />
          <Route exact path='/job/:id' component={Job} />
          <Route exact path='/jobs-overview' component={JobsOverview} />
          <Route exact path='/job/:id/candidates' component={JobApplicants} />
          <Route exact path='/job/:id/candidates/:page' component={JobApplicants} />
          <Route exact path='/job/:id/candidates/:page/:profile_id' component={Profile} />
        </div>
      </section>
      <footer className="footer">
        <div className="content has-text-centered">
          <p>
            {randomFooterMessages}
          </p>
        </div>
      </footer>
    </Router>
  );
}

export default App
