import React, { useState } from 'react'
import { Link, useParams } from 'react-router-dom'

import OutlineButton from './global/buttons/OutlineButton'
import PrimaryButton from './global/buttons/PrimaryButton'
import { Turquoise } from './global/colors'
import Controls from './global/Controls'
import ShareButton from './global/buttons/ShareButton'
import SaveButton from './global/buttons/SaveButton'
import InlineIconLabel from './global/InlineIconLabel'
import TextButton from './global/buttons/TextButton'
import { candidatesDb } from './global/mock'
import BackArrowButton from './global/buttons/BackArrowButton'
import NextArrowButton from './global/buttons/NextArrowButton'
import Avatar from './global/Avatar'

const sortTechStack = tech_stack => {
  function compare(a, b) {
    if (a.name < b.name) {
      return -1;
    }
    if (a.name > b.name) {
      return 1;
    }
    return 0;
  }
  let allTech = {
    0: [],
    1: [],
    2: [],
    3: [],
  }
  tech_stack.map(tech => {
    allTech[tech.experience].push(tech)
  })

  allTech[0].sort(compare)
  allTech[1].sort(compare)
  allTech[2].sort(compare)
  allTech[3].sort(compare)

  return [...allTech[3], ...allTech[2], ...allTech[1], ...allTech[0]]
}

function Profile({ checked: checkedProp, ...rest }) {
  let { id, page, profile_id } = useParams()
  const [showMore, setShowMore] = useState(false)

  let socialButtons = []
  let profile = candidatesDb[profile_id]
  profile.tech_stack = sortTechStack(profile.tech_stack)

  if (profile.github_url) {
    socialButtons.push(
      <a href={profile.github_url} target='_blank' rel="noopener noreferrer">
        <OutlineButton
          label='Github'
          icon='fab fa-github'
        />
      </a>
    )
  }

  if (profile.stackoverflow_url) {
    socialButtons.push(
      <a href={profile.stackoverflow_url} target='_blank' rel="noopener noreferrer">
        <OutlineButton
          label='StackOverflow'
          icon='fab fa-stack-overflow'
        />
      </a>
    )
  }


  return (
    <div>
      <Controls
        left={<Link to={'/job/' + id + '/candidates/' + page}><BackArrowButton label='Jobs Overview' /></Link>}
      />
      <div className="columns">
        <div className="column is-4">
          <Avatar size={256} src={profile.profile_picture} />
          <div style={{ marginTop: 20 }}>
            {profile.tech_stack && profile.tech_stack.map(tech => (
              <div style={{
                backgroundColor: `${tech.experience === 3 ? '#7F4CDC' : ''}${tech.experience === 2 ? '#A079E5' : ''}${tech.experience === 1 ? '#CCB8F1' : ''}`,
                float: 'left',
                color: '#FFFFFF',
                fontWeight: 'bold',
                margin: 4,
                paddingTop: 4,
                paddingBottom: 4,
                paddingLeft: 12,
                paddingRight: 12,
              }}>{tech.name}</div>
            ))}
          </div>
        </div>
        <div className="column is-8">

          <div className='columns is-gapless is-marginless'>
            <div className='column'>
              <span>
                <h1 style={{ display: 'inline', marginRight: 12 }}>{profile.name}</h1>
                <InlineIconLabel icon='fas fa-map-marker'>
                  {profile.location}
                </InlineIconLabel>
              </span>
            </div>
            <div className='column' style={{ textAlign: 'right' }}>
              <span style={{ backgroundColor: '#D1D1D6', color: '#9B9B9B', borderRadius: 12, paddingLeft: 24, paddingRight: 24 }}>Member since 2020</span>
            </div>
          </div>

          <div style={{ fontWeight: 600, fontSize: 14, marginBottom: 12 }}>
            <InlineIconLabel icon='fas fa-school'>
              {profile.education}
            </InlineIconLabel>
          </div>


          <Controls
            style={{ marginBottom: 12 }}
            left={socialButtons}
          />
          <Controls
            style={{ marginBottom: 12 }}
            left={[
              <PrimaryButton
                label='Match'
              />,
              <TextButton
                label={`This Profile doesn't fit`}
              />]}

          />

          <div className='columns is-mobile' style={{ borderBottom: '1px solid #E4E4E4' }}>
            <div className='column'>
              <div style={{ color: '#9B9B9B', fontWeight: 'bold' }}>
                Last 7 days
              </div>
              <div>
                <InlineIconLabel icon='fas fa-rocket'>
                  <strong>{profile.job_matches}</strong> Job Matches
              </InlineIconLabel>
              </div>
              <div>
                <InlineIconLabel icon='fas fa-comment-dots'>
                  <strong>{profile.active_processes}</strong> Active Processes
                </InlineIconLabel>
              </div>
            </div>
            <div className='column' style={{ paddingTop: 60 }}>
              <Controls
                right={[
                  // <SaveButton />,
                  <ShareButton />]}
              />
            </div>
          </div>

          <h4 style={{ color: '#9B9B9B' }}>WORK</h4>
          <div style={{ borderBottom: '1px solid #E4E4E4', paddingTop: 12, paddingBottom: 12, marginBottom: 12 }}>
            {!showMore && profile.work && profile.work.slice(0, 2).map(work => (
              <div key={work.company.name + work.role} className='columns'>
                <div className=' column is-2'>
                  <figure className="image" style={{ width: 64, height: 64, margin: 'auto' }}>
                    <img
                      className="is-rounded"
                      src={work.company.profile_picture}
                      alt=''
                      style={{ objectFit: 'cover', objectPosition: 'center center', width: '100%', height: '100%' }}
                    />
                  </figure>
                </div>
                <div className=' column is-10'>
                  <div><h5>{work.role}</h5></div>
                  <div><h6>{work.company.name}</h6></div>
                  <div style={{ color: '#D1D1D6' }}>{work.start_date} - {work.end_date}</div>
                  <div>{work.description}</div>
                </div>

              </div>
            ))}
            {showMore && profile.work && profile.work.map(work => (
              <div key={work.company.name + work.role} className='columns'>
                <div className=' column is-2'>
                  <figure className="image" style={{ width: 64, height: 64, margin: 'auto' }}>
                    <img
                      className="is-rounded"
                      src={work.company.profile_picture}
                      alt=''
                      style={{ objectFit: 'cover', objectPosition: 'center center', width: '100%', height: '100%' }}
                    />
                  </figure>
                </div>
                <div className=' column is-10'>
                  <div><h5>{work.role}</h5></div>
                  <div><h6>{work.company.name}</h6></div>
                  <div style={{ color: '#D1D1D6' }}>{work.start_date} - {work.end_date}</div>
                  <div>{work.description}</div>
                </div>

              </div>
            ))}
            {!showMore && profile.work && profile.work.length > 2 && <div
              style={{ fontWeight: 'bold', cursor: 'pointer', color: Turquoise }}
              onClick={event => setShowMore(!showMore)}
            >
              Show {profile.work.length - 2} More Experiences <i className='fas fa-chevron-down' />
            </div>}

            {showMore && profile.work && profile.work.length > 2 && <div
              style={{ fontWeight: 'bold', cursor: 'pointer', color: Turquoise }}
              onClick={event => setShowMore(!showMore)}
            >
              Show Less Experiences <i className='fas fa-chevron-up' />
            </div>}
          </div>
          <h4 style={{ color: '#9B9B9B' }}>QUALIFICATIONS</h4>
          <div style={{ marginTop: 8 }}>
            {profile.qualifications && profile.qualifications.map(qualification => (
              <div key={qualification}>• {qualification}</div>
            ))}
          </div>
        </div>
      </div>
    </div>
  )
}

export default Profile