import React, { useRef, useState } from 'react'
import IconLabelButton from './IconLabelButton'
import { Turquoise } from '../colors'

function ShareButton() {

  const [copySuccess, setCopySuccess] = useState('Copy')
  const [open, setOpen] = useState(false)
  const textAreaRef = useRef(null);

  function copyToClipboard(e) {
    textAreaRef.current.select();
    document.execCommand('copy');
    e.target.focus();
    setCopySuccess('Copied');
  };


  return (
    <div>
      {open && <div className="modal is-active">
        <div className="modal-background" onClick={() => setOpen(false)}></div>
        <div className="modal-content" style={{ backgroundColor: 'white', padding: 20 }}>
          <h3>Share this Job</h3>
          <div className='columns' style={{marginTop: 12}}>
            <div class='column'>
              <Icon color='#0077B5' className='fab fa-linkedin-in' style={{ fontSize: 28, top: 'calc(50% - 12px)', left: 'calc(50% - 12px)' }} />
            </div>
            <div class='column'>
              <Icon color='#3B5998' className='fab fa-facebook-f' style={{ fontSize: 32, top: 'calc(50% - 15px)', left: 'calc(50% - 10px)' }} />
            </div>
            <div class='column'>
              <Icon color='#25D366' className='fab fa-whatsapp' style={{ fontSize: 32, top: 'calc(50% - 15px)', left: 'calc(50% - 13px)' }} />
            </div>
            <div class='column'>
              <Icon color='#0079BF' className='fab fa-trello' style={{ fontSize: 32, top: 'calc(50% - 15px)', left: 'calc(50% - 14px)' }} />
            </div>
            <div class='column'>
              <Icon color='#1E96C8' className='fab fa-telegram-plane' style={{ fontSize: 32, top: 'calc(50% - 15px)', left: 'calc(50% - 16px)' }} />
            </div>
            <div class='column'>
              <Icon color='#611f69' className='fab fa-slack' style={{ fontSize: 32, top: 'calc(50% - 15px)', left: 'calc(50% - 14px)' }} />
            </div>
          </div>
          <p>Or copy the link below</p>
          <div className="control has-icons-right">
            <input
              className="input"
              ref={textAreaRef}
              type="text"
              value='https://sboj.io'
            />
            <span
              className="icon is-right"
              style={{ pointerEvents: 'initial', cursor: 'pointer', marginRight: 12, color: Turquoise, fontWeight: 'bold' }}
              onClick={copyToClipboard}
            >
              {copySuccess}
            </span>
          </div>
        </div>
        <button className="modal-close is-large" aria-label="close" onClick={() => setOpen(false)}></button>
      </div>}
      <IconLabelButton
        label='Share'
        icon='fas fa-share-square'
        style={{ fontSize: 16, color: '#D1D1D6' }}
        labelStyle={{ color: '#D1D1D6', fontWeight: 600, fontSize: 12 }}
        onClick={() => setOpen(true)}
      />
    </div>
  );
}

export default ShareButton

function Icon(params) {
  return (
    <div style={{
      width: 64,
      height: 64,
      borderRadius: 32,
      backgroundColor: params.color,
    }}>
      <i style={{ color: '#FFFFFF', position: 'relative', ...params.style }} className={params.className} />
    </div>
  )
}